'use strict';

module.exports = function turkishtoEnglish (str) {
    return str.replace(/[]+/g,'s')
        .replace(/[]+/g,'S')
        .replace(/[]+/g,'U')
        .replace(/[]+/g,'u')
        .replace(/[]+/g,'O')
        .replace(/[]+/g,'o')
        .replace(/[]+/g,'I')
        .replace(/[]+/g,'i')
        .replace(/[]+/g,'g')
        .replace(/[]+/g,'G')
        .replace(/[]+/g,'C')
        .replace(/[]+/g,'c')
};
